<template>
  <select v-model="selected" @change="onChange(selected)" class="mb-3 form-control custom-select">
    <optgroup v-for="(year) in Object.keys(options).reverse()" :label="year">
      <option v-for="month in options[year]" :value="year + '/' + month.id">
        {{month.label}}
      </option>
    </optgroup>
  </select>
</template>

<script>
  import parse from 'url-parse';
  const MONTH_LABELS = {
    1: "Leden",
    2: "Únor",
    3: "Březen",
    4: "Duben",
    5: "Květen",
    6: "Červen",
    7: "Červenec",
    8: "Srpen",
    9: "Září",
    10: "Říjen",
    11: "Listopad",
    12: "Prosinec"
  };

  const buildDefaultSelected = () => {
    const url = parse(window.location.href);
    const eventPathItems = url.pathname.split("/")
      .filter((item) => !!item)
      .slice(-2);

    return eventPathItems.join("/")
  };

  const defaultRedirect = (selected) => {
    const url = parse(window.location.href);
    const basePathItems = url.pathname.split("/")
      .filter((item) => !!item)
      .slice(0, -2);

    basePathItems.push(selected);

    window.location.href = "/" + basePathItems.join("/") + "#content"
  };

  export default {
    props: {
      eventsIndex: {
        type: Array,
        required: true
      },
      selectedItem: {
        type: String,
        required: false
      },
      onChange: {
        type: Function,
        required: false,
        default: defaultRedirect
      }
    },
    data() {
      return {
        selected: this.selectedItem ? this.selectedItem : buildDefaultSelected()
      }
    },
    computed: {
      options() {
        return this.eventsIndex
          /*.sort((a, b) => {
            if ((a.year - b.year) === 0) {
              return a.month - b.month;
            } else {
              return b.year - a.year
            }
          })*/
          .reduce((result, event) => {
            if (!result[event.year]) {
              result[event.year] = []
            }

            result[event.year].push({
              id: event.month,
              label: MONTH_LABELS[event.month]
            });
            return result
          }, {})
      }
    }
  }
</script>
