<template>
  <div :id="id"></div>
</template>

<script>
  import $ from "jquery";
  import parse from 'url-parse';

  const buildDefaultUrl = () => {
    const url = parse(window.location.href);
    return url.hash.replace(/^#/, '');
  };

  export default {
    props: {
      url: {
        type: String,
        required: false
      },
      selector: {
        type: String,
        required: true,
        default: ""
      }
    },
    data () {
      return {
        id: null,
        targetUrl: this.url ? this.url : buildDefaultUrl()
      }
    },
    mounted() {
      this.id = this._uid;
      this.$nextTick(function () {
        $(`#${this.id}`).load(`${this.targetUrl}${this.selector ? ' ' + this.selector : ""}`);
      })
    }
  }
</script>
