import VueRouter from "vue-router";

import TopNEvents from './components/events/topn'
import Program from './components/events/program'
import NotFound from './components/NotFound'

const routes = [
  {
    path: '/',
    name: 'home',
    component: TopNEvents
  },
  {
    path: '/program/:year/:month',
    name: 'program',
    component: Program
  },
  {
    path: '*',
    name: 'not-found',
    component: NotFound
  },
  // { path: '/bar', component: Bar }
];

export default new VueRouter({
  routes
});
