import Vue from 'vue'
import VueRouter from "vue-router";
import VueFilterDateFormat from 'vue-filter-date-format'
import VCalendar from 'v-calendar'
import Calendar from './components/Calendar'
import GSearch from './components/GoogleSeach'
import EventsDateSelect from './components/events/EventsDateSelect'
import router from './router';

Vue.use(VCalendar, {
  formats: {
    title: 'MMMM YYYY',
    weekdays: 'W',
    navMonths: 'MMM',
    input: ['L', 'YYYY-MM-DD', 'YYYY/MM/DD'],
    dayPopover: 'L',
  },
  locale: 'cs-CZ'
});

Vue.use(VueFilterDateFormat);

Vue.mixin({
  data: () => {
    return {
      BASE_URL: BASE_URL,
      EVENT_CALENDAR: EVENT_CALENDAR
    }
  }
});

/**
 * header
 */
new Vue({
    el: '#header',
    components: {
      Calendar,
      GSearch
    }
});

/**
 * spa
 */
if (document.getElementById('spa')) {
  Vue.use(VueRouter);
  new Vue({
    el: '#spa',
    router
  });
}

if (document.getElementById('eventsDateSelect')) {
  new Vue({
    el: '#eventsDateSelect',
    components: {
      EventsDateSelect
    }
  })
}
