<template>
  <div class="container shadow bg-white p-5">
    <div class="row">
      <events-date-select :selected-item="selected"
                          :events-index="EVENT_CALENDAR"
                          :on-change="redirect"></events-date-select>
    </div>
    <div v-for="(key, value) in fragments" class="row animated fadeIn slow">
      <transition name="fade">
        <loading-page-fragment v-if="selected === key"
                               selector="#program-events"></loading-page-fragment>
      </transition>
    </div>
  </div>
</template>

<script>
  import LoadingPageFragment from '../../LoadingPageFragment';
  import EventsDateSelect from '../../events/EventsDateSelect';
  import parse from 'url-parse'

  const defaultSelected = () => {
    const url = parse(window.location.href);
    const eventPathItems = url.hash.split("/")
      .filter((item) => !!item)
      .slice(-2);

    return eventPathItems.join("/")
  };

  export default {
    data() {
      const selected = defaultSelected();
      const fragments = {};
      fragments[selected] = selected;
      return {
        fragments,
        selected
      }
    },
    computed: {
    },
    methods: {
      redirect(selected) {
        const url = parse(window.location.href);
        const basePathItems = url.hash.replace(/^#/, '').split("/")
          .filter((item) => !!item)
          .slice(0, -2);

        basePathItems.push(selected);

        window.location.hash = "#/" + basePathItems.join("/");

        this.fragments[selected] = selected;
        this.selected = selected;
      }
    },
    components: {
      LoadingPageFragment,
      EventsDateSelect
    }
  }
</script>

<style lang="scss">
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
    #program {
      transition: opacity 1s;
    }
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    #program {
      opacity: 0;
    }
  }
</style>
